@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Segoe UI", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  min-height: 100vh;
  background-color: #e3e3e3;
}

:root {
  --dark-bg: #e3e3e3;
  --light-bg: #f6f6f8;
  --text-black: #212121;
  --text-red: #c60021;
  --grey-bg: #263238;
  --bordo-bg: #ab003c;
}

.wrapper {
  display: flex;
}

.wrapper-empPage {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-bottom: 30px;
}

.card {
  border: 1px solid #e3e3e3;
  border: 1px solid var(--dark-bg);
  box-shadow: -1px 11px 9px -8px rgba(0, 0, 0, 0.75);
  width: 250px;
  margin: 10px;
  border-radius: 10px;
  background-color: #f6f6f8;
  background-color: var(--light-bg);
  min-height: 200px;
}

.wrapper-dob {
  min-height: 100vh;
  width: 350px;
  background-color: #f6f6f8;
  background-color: var(--light-bg);
  color: #212121;
  color: var(--text-black);
  padding-top: 5%;
  position: -webkit-sticky; /* Safari */
  position: sticky;
  top: 0;
}

.title-card {
  background-color: #263238;
  background-color: var(--grey-bg);
  height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
  color: #f6f6f8;
  color: var(--light-bg);
  margin-top: 7%;
  font-size: 1.2rem;
}

.card-content {
  margin: 5%;
}

.heading {
  padding: 5px;
  color: #f6f6f8;
  color: var(--light-bg);
  background-color: #263238;
  background-color: var(--grey-bg);
}

.heading-dob {
  background-color: #ab003c;
  background-color: var(--bordo-bg);
}

.dob-content {
  color: #212121;
  color: var(--text-black);
}

.dob-title {
  font-size: 1.1rem;
}

.dob-div {
  border-bottom: 1px solid #e3e3e3;
  border-bottom: 1px solid var(--dark-bg);
}

